import axios from '../axios';

const getuserdata_report = () => {
    axios.get('api/v1/appdata/export_profile_xls/', {
        headers: {
            'Content-Type': 'application/ms-excel',
        },
        responseType: 'blob'
    }).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'userdata_report.xls');
        document.body.appendChild(link);
        link.click();
    });

};

export default getuserdata_report;