import axios from '../axios';
import headers from '../headers';

const getGamedatalog = async (instance:any, level: any, user: any) => {

    return (await axios.get(`api/v1/appdata/gamedatalog/?instance=${instance}&level_sublevel=${level}&username=${user}`, { "headers": headers(String(localStorage.getItem("AuthToken"))) }));
};

export default getGamedatalog;